.tile{
    display: grid;
    place-content: center;
    width: 50px;
    height:50px;
    background-repeat: no-repeat;
}
.tile .King{
    width:50px;
    height:50px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 40px;
}
.tile .King:hover{
    cursor:grab;
}
.tile .King:active{
    cursor:grabbing;
}
.tile .Knight{
    width:40px;
    height:40px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 35px;
}
.tile .Knight:hover{
    cursor:grab;
}
.tile .Knight:active{
    cursor:grabbing;
}
.black-tile{
    background-color: #769656;
}
.white-tile{
    background-color:white;
}
.tile img{
    width: 40px;
}